import ComplexNumber from './complex-number';
import Point from './point';
import transform from './transform';
import { PRECISION, DEG_TO_RAD } from './constants';
import round from '../common/round';

function numberSign(x) {
    return x < 0 ? -1 : 1;
}

export function solveQuadraticEquation(a, b, c) {
    const squareRoot = Math.sqrt(Math.pow(b, 2) - 4 * a * c);
    return [
        (-b + squareRoot) / (2 * a),
        (-b - squareRoot) / (2 * a)
    ];
}

//Cardano's formula
export function solveCubicEquation(a, b, c, d) {
    if (a === 0) {
        return solveQuadraticEquation(b, c, d);
    }

    const p = (3 * a * c - Math.pow(b, 2)) / (3 * Math.pow(a, 2));
    const q = (2 * Math.pow(b, 3) - 9 * a * b * c + 27 * Math.pow(a, 2) * d) / (27 * Math.pow(a, 3));
    const Q = Math.pow(p / 3, 3) + Math.pow(q / 2, 2);
    const i = new ComplexNumber(0,1);
    const b3a = -b / (3 * a);
    let x1, x2, y1, y2, y3, z1, z2;

    if (Q < 0) {
        x1 = new ComplexNumber(-q / 2, Math.sqrt(-Q)).nthRoot(3);
        x2 = new ComplexNumber(-q / 2, - Math.sqrt(-Q)).nthRoot(3);
    } else {
        x1 = -q / 2 + Math.sqrt(Q);
        x1 = new ComplexNumber(numberSign(x1) * Math.pow(Math.abs(x1), 1 / 3));
        x2 = -q / 2 - Math.sqrt(Q);
        x2 = new ComplexNumber(numberSign(x2) * Math.pow(Math.abs(x2), 1 / 3));
    }

    y1 = x1.add(x2);

    z1 = x1.add(x2).multiplyConstant(-1 / 2);
    z2 = x1.add(x2.negate()).multiplyConstant(Math.sqrt(3) / 2);

    y2 = z1.add(i.multiply(z2));
    y3 = z1.add(i.negate().multiply(z2));

    const result = [];

    if (y1.isReal()) {
        result.push(round(y1.real + b3a, PRECISION));
    }
    if (y2.isReal()) {
        result.push(round(y2.real + b3a, PRECISION));
    }
    if (y3.isReal()) {
        result.push(round(y3.real + b3a, PRECISION));
    }

    return result;
}

function toCubicPolynomial(points, field) {
    return [ -points[0][field] + 3 * points[1][field] - 3 * points[2][field] + points[3][field],
        3 * (points[0][field] - 2 * points[1][field] + points[2][field]),
        3 * (-points[0][field] + points[1][field]),
        points[0][field]
    ];
}

export function calculateCurveAt(t, field, points) {
    const t1 = 1 - t;
    return Math.pow(t1, 3) * points[0][field] +
        3 * Math.pow(t1, 2) * t * points[1][field] +
        3 * Math.pow(t, 2) * t1 * points[2][field] +
        Math.pow(t, 3) * points[3][field];
}

export function curveIntersectionsCount(points, point, bbox) {
    const polynomial = toCubicPolynomial(points, "x");
    const roots = solveCubicEquation(polynomial[0], polynomial[1], polynomial[2], polynomial[3] - point.x);
    let rayIntersection, intersectsRay;
    let count = 0;
    for (let i = 0; i < roots.length; i++) {
        rayIntersection = calculateCurveAt(roots[i], "y", points);
        intersectsRay = close(rayIntersection, point.y) || rayIntersection > point.y;
        if (intersectsRay && (((roots[i] === 0 || roots[i] === 1) && bbox.bottomRight().x > point.x) || (0 < roots[i] && roots[i] < 1))) {
            count++;
        }
    }

    return count;
}

export function lineIntersectionsCount(a, b, point) {
    let intersects;
    if (a.x !== b.x) {
        const minX = Math.min(a.x, b.x);
        const maxX = Math.max(a.x, b.x);
        const minY = Math.min(a.y, b.y);
        const maxY = Math.max(a.y, b.y);
        const inRange = minX <= point.x && point.x < maxX;

        if (minY === maxY) {
            intersects = point.y <= minY && inRange;
        } else {
            intersects = inRange && (((maxY - minY) * ((a.x - b.x) * (a.y - b.y) > 0 ? point.x - minX : maxX - point.x)) / (maxX - minX) + minY - point.y) >= 0;
        }
    }
    return intersects ? 1 : 0;
}

export function lineIntersection(p0, p1, p2, p3) {
    const s1x = p1.x - p0.x;
    const s2x = p3.x - p2.x;
    const s1y = p1.y - p0.y;
    const s2y = p3.y - p2.y;
    const nx = p0.x - p2.x;
    const ny = p0.y - p2.y;
    const d = s1x * s2y - s2x * s1y;
    const s = (s1x * ny - s1y * nx) / d;
    const t = (s2x * ny - s2y * nx) / d;

    if (s >= 0 && s <= 1 && t >= 0 && t <= 1) {
        return new Point(p0.x + t * s1x, p0.y + t * s1y);
    }
}

export function close(a, b, tolerance = PRECISION) {
    return round(Math.abs(a - b), tolerance) === 0;
}

export function closeOrLess(a, b, tolerance) {
    return a < b || close(a, b, tolerance);
}

export function isOutOfEndPoint(endPoint, controlPoint, point) {
    const angle = deg(Math.atan2(controlPoint.y - endPoint.y, controlPoint.x - endPoint.x));
    const rotatedPoint = point.transformCopy(transform().rotate(-angle, endPoint));

    return rotatedPoint.x < endPoint.x;
}

export function hasRootsInRange(points, point, field, rootField, range) {
    const polynomial = toCubicPolynomial(points, rootField);
    const roots = solveCubicEquation(polynomial[0], polynomial[1], polynomial[2], polynomial[3] - point[rootField]);
    let intersection;

    for (let idx = 0; idx < roots.length; idx++) {
        if (0 <= roots[idx] && roots[idx] <= 1) {
            intersection = calculateCurveAt(roots[idx], field, points);
            if (Math.abs(intersection - point[field]) <= range) {
                return true;
            }
        }
    }
}

export function deg(radians) {
    return radians / DEG_TO_RAD;
}

export function rad(degrees) {
    return degrees * DEG_TO_RAD;
}

export function ellipseExtremeAngles(center, rx, ry, matrix) {
    let extremeX = 0;
    let extremeY = 0;

    if (matrix) {
        extremeX = Math.atan2(matrix.c * ry, matrix.a * rx);
        if (matrix.b !== 0) {
            extremeY = Math.atan2(matrix.d * ry, matrix.b * rx);
        }
    }

    return {
        x: extremeX,
        y: extremeY
    };
}


